import React, { Component } from "react";

import { asElement, isHTMLElement } from "../lib/pdfjs-dom";
import "../style/MouseSelection.css";

import type { LTWH } from "../types.js";

interface Coords {
	x: number;
	y: number;
}

interface State {
	locked: boolean;
	start: Coords | null;
	end: Coords | null;
}

interface Props {
	onSelection: (
		startTarget: HTMLElement,
		boundingRect: LTWH,
		resetSelection: () => void
	) => void;
	onDragStart: () => void;
	onDragEnd: () => void;
	shouldStart: (event: PointerEvent) => boolean;
	onChange: (isVisible: boolean) => void;
}

class MouseSelection extends Component<Props, State> {
	state: State = {
		locked: false,
		start: null,
		end: null,
	};

	root?: HTMLElement;

	reset = () => {
		const { onDragEnd } = this.props;

		onDragEnd();
		this.setState({ start: null, end: null, locked: false });
	};

	getBoundingRect(start: Coords, end: Coords): LTWH {
		return {
			left: Math.min(end.x, start.x),
			top: Math.min(end.y, start.y),

			width: Math.abs(end.x - start.x),
			height: Math.abs(end.y - start.y),
		};
	}

	componentDidUpdate() {
		const { onChange } = this.props;
		const { start, end } = this.state;

		const isVisible = Boolean(start && end);

		onChange(isVisible);
	}

	componentDidMount() {
		if (!this.root) {
			return;
		}

		const that = this;

		const { onSelection, onDragStart, onDragEnd, shouldStart } = this.props;

		const container = asElement(this.root.parentElement);

		if (!isHTMLElement(container)) {
			return;
		}

		let containerBoundingRect: DOMRect | null = null;

		const containerCoords = (pageX: number, pageY: number) => {
			if (!containerBoundingRect) {
				containerBoundingRect = container.getBoundingClientRect();
			}

			return {
				x: pageX - containerBoundingRect.left + container.scrollLeft,
				y:
					pageY -
					containerBoundingRect.top +
					container.scrollTop -
					window.scrollY,
			};
		};

		container.addEventListener("pointermove", (event: PointerEvent) => {
			const { start, locked } = this.state;

			if (!start || locked) {
				return;
			}

			that.setState({
				...this.state,
				end: containerCoords(event.pageX, event.pageY),
			});
		});

		container.addEventListener("pointerdown", (event: PointerEvent) => {
			// This is where we can intercept the touch if we don't want to draw a highlight -- i.e., if we're scrolling
			if (event.pointerType === "touch") {
				container.classList.add("touchDefault");
				container.classList.remove("touchNone");
				return;
			} else {
				container.classList.remove("touchDefault");
				container.classList.add("touchNone");
			}
			event.preventDefault();

			if (!shouldStart(event)) {
				this.reset();
				return;
			}

			const startTarget = asElement(event.target);
			if (!isHTMLElement(startTarget)) {
				return;
			}

			onDragStart();

			this.setState({
				start: containerCoords(event.pageX, event.pageY),
				end: null,
				locked: false,
			});

			const onMouseUp = (event: PointerEvent): void => {
				// emulate listen once
				event.currentTarget?.removeEventListener(
					"pointerup",
					onMouseUp as EventListener
				);

				const { start } = this.state;

				if (!start) {
					return;
				}

				const end = containerCoords(event.pageX, event.pageY);

				const boundingRect = that.getBoundingRect(start, end);

				if (
					!isHTMLElement(event.target) ||
					!container.contains(asElement(event.target)) ||
					!that.shouldRender(boundingRect)
				) {
					that.reset();
					return;
				}

				that.setState(
					{
						end,
						locked: true,
					},
					() => {
						const { start, end } = that.state;

						if (!start || !end) {
							return;
						}

						if (isHTMLElement(event.target)) {
							onSelection(startTarget, boundingRect, that.reset);

							onDragEnd();
						}
					}
				);
			};

			const { ownerDocument: doc } = container;
			if (doc.body) {
				doc.body.addEventListener("pointerup", onMouseUp);
			}
		});
	}

	shouldRender(boundingRect: LTWH) {
		return boundingRect.width >= 1 && boundingRect.height >= 1;
	}

	render() {
		const { start, end } = this.state;

		return (
			<div
				className="MouseSelection-container"
				ref={(node) => {
					if (!node) {
						return;
					}
					this.root = node;
				}}
			>
				{start && end ? (
					<div
						className="MouseSelection"
						style={this.getBoundingRect(start, end)}
					/>
				) : null}
			</div>
		);
	}
}

export default MouseSelection;
